import Vue from 'vue';

import './goTop.css';

export default Vue.component('goTop',{
  template:require('./goTop.html'),
  data(){
    return {
      flag:true,
      timer:null,
      scrollTop:null,
      status:'none'
    }
  },
  props:{
    spend:{
      type:Number,
      default:30
    }
  },
  mounted() {
    var that = this;
    window.onscroll = function(){
      that.height = document.documentElement.clientHeight || document.body.clientHeight;
      if(that.scrollTop >= that.height){
        that.status = 'block';
      }else{
        that.status = 'none';
      }
      if(!that.flag){
        clearInterval(that.timer);
        that.timer = null;
      }
      that.flag = false;
      that.scrollTop=document.documentElement.scrollTop || document.body.scrollTop;
    };
  },
  methods:{
    top() {
      var that = this;
      clearInterval(that.timer);
      that.timer = setInterval(function(){
        that.now = that.scrollTop;
        that.speed = ( 0- that.now)/10;
        that.speed = that.speed > 0 ? Math.ceil(that.speed):Math.floor(that.speed);
        if(that.scrollTop == 0){
          clearInterval(that.timer);
          that.timer = null;
        }
        that.flag = true;
        document.documentElement.scrollTop = that.scrollTop + that.speed;
        document.body.scrollTop = that.scrollTop + that.speed;
      }, this.spend);
    }
  }
})



